import React from 'react'
import { Container, Row, Col } from 'reus'
import styled from '@emotion/styled'
import { StaticQuery, graphql } from 'gatsby'
import { Headline, Item } from 'Components'

const WhyUs = () => {
  const FeatureWrapped = styled.div`
    label: feature;
    padding: 70px 0 30px 0px;
  `
  
  return (
    <StaticQuery
      query={graphql`
        query HeadingQuery {
          attendance: file(relativePath: { eq: "attendance.png" }) {
            childImageSharp {
              fixed(width: 80) {
                ...GatsbyImageSharpFixed
              }
            }
          }
          network: file(relativePath: { eq: "attendance.png" }) {
            childImageSharp {
              fixed(width: 80) {
                ...GatsbyImageSharpFixed
              }
            }
          }
          team: file(relativePath: { eq: "team.png" }) {
            childImageSharp {
              fixed(width: 80) {
                ...GatsbyImageSharpFixed
              }
            }
          }
          cloud: file(relativePath: { eq: "cloud.png" }) {
            childImageSharp {
              fixed(width: 80) {
                ...GatsbyImageSharpFixed
              }
            }
          }
          time: file(relativePath: { eq: "time.png" }) {
            childImageSharp {
              fixed(width: 80) {
                ...GatsbyImageSharpFixed
              }
            }
          }
          secure: file(relativePath: { eq: "secure.png" }) {
            childImageSharp {
              fixed(width: 80) {
                ...GatsbyImageSharpFixed
              }
            }
          }
        }
      `}
      render={data => {
        return (
          <FeatureWrapped>
              <Container md gutter={22}>
                <Headline
                  title="Why us?"
                  desc="The world of HR is complex – that’s why we built a flexible platform for the entire HR ecosystem."
                />
              </Container>
              <Container lg gutter={22}>
                <Row >
                  <Col lg={4} md={6} sm={12}>
                    <Item
                      title="Simple platform, paperless, faster, secure"
                      image={data.secure}
                    />
                  </Col>
                  <Col lg={4} md={6} sm={12}>
                    <Item
                      title="Employees’ database"
                      image={data.team}
                    />
                  </Col>
                  <Col lg={4} md={6} sm={12}>
                    <Item
                      title="Easy use mobile-based attendance system "
                      image={data.time}
                    />
                  </Col>
                  <Col lg={4} md={6} sm={12}>
                    <Item
                      title="Compensation (payroll) management with minimal human intervention"
                      image={data.attendance}
                    />
                  </Col>
                  <Col lg={4} md={6} sm={12}>
                    <Item
                      title="Cloud-based, conveniently accessible anywhere, anytime "
                      image={data.cloud}
                    />
                  </Col>
                  <Col lg={4} md={6} sm={12}>
                    <Item
                      title="Vast expandable network of partners/financial services"
                      image={data.network}
                    />
                  </Col>
                </Row>
            </Container>
          </FeatureWrapped>
        )
      }}
    />
    
  )
}

export default WhyUs