import React from 'react'
import { css } from '@emotion/core'
import { Container, Typography, Row, Col } from 'reus'
import styled from '@emotion/styled'
import { StaticQuery, graphql } from 'gatsby'
import { Headline, LearnMoreButton, TitleDesc, ImgWrapper } from 'Components'
import Img from 'gatsby-image'

const Service = () => {
  const FeatureWrapped = styled.div`
    label: feature;
    padding: 70px 0 30px 0px;
  `

  const ServiceItem = styled.div`
    padding: 70px 0;
  `
  const TestimonyWrapper = styled.div`
    padding: 50px 0;
  `
  return (
    <StaticQuery
      query={graphql`
        query ServiceQuery {
          attendance: file(relativePath: { eq: "attendance.png" }) {
            childImageSharp {
              fixed(width: 80) {
                ...GatsbyImageSharpFixed
              }
            }
          }
          attendanceLeaveModana: file(relativePath: { eq: "attendance-leave-modana.png" }) {
            childImageSharp {
              fixed(width: 80) {
                ...GatsbyImageSharpFixed
              }
            }
          }
          payroll: file(relativePath: { eq: "payroll-modana.png" }) {
            childImageSharp {
              fluid(maxWidth: 600) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      `}
      render={data => {
        return (
          <>
            <FeatureWrapped>
                <Container xl gutter={22}>
                  <Headline
                    title="Services"
                    desc="The services that help your company start from Managing your employee information, Attendance, Payroll disbursement, and Employee lending."
                  />
                  <div>
                    <ServiceItem>
                      <Row gutter={30} align="center">
                        <Col sm={6}>
                          <TitleDesc
                            title="Human Resource"
                            desc="With Modana human resources (HR) services, obtaining information becomes a speedy process as all you need to do is type in what you’re searching for. You only need to enter the data once, such as a salary slip, new payroll policy. Any changes made are automatically updated wherever it is assigned without having to manually change it everywhere."
                          />
                          <LearnMoreButton>
                            Learn More
                          </LearnMoreButton>
                        </Col>
                        <Col sm={6}>
                          <ImgWrapper>
                            <Img fluid={data.payroll.childImageSharp.fluid} />
                          </ImgWrapper>
                        </Col>
                      </Row>
                    </ServiceItem>
                    <ServiceItem>
                      <Row gutter={30} align="center" css={css`
                        flex-direction: row-reverse;
                      `}>
                        <Col sm={6}>
                          <TitleDesc
                            title="Payroll"
                            desc="Forget the hassle cash-based payroll payment and leave it to Modana to handle it. Distributing cashless payroll to each of your employee. Easy and secure. "
                          />
                          <LearnMoreButton>
                            Learn More
                          </LearnMoreButton>
                        </Col>
                        <Col sm={6}>
                          <ImgWrapper>
                            <Img fluid={data.payroll.childImageSharp.fluid} />
                          </ImgWrapper>
                        </Col>
                      </Row>
                    </ServiceItem>
                    <ServiceItem>
                      <Row gutter={30} align="center">
                        <Col sm={6}>
                          <TitleDesc
                            title="Lending"
                            desc="Since we are providing method of payback through automate debit from payroll, we could minimize the NPL. So do not worry much about delinquency. Work Hard, Gets More!"
                          />
                          <LearnMoreButton>
                            Learn More
                          </LearnMoreButton>
                        </Col>
                        <Col sm={6}>
                          <ImgWrapper>
                            <Img fluid={data.payroll.childImageSharp.fluid} />
                          </ImgWrapper>
                        </Col>
                      </Row>
                    </ServiceItem>
                  </div>
                </Container>
            </FeatureWrapped>
            <TestimonyWrapper>
              <Container xl gutter={22}>
                <Row align="center" gutter={20}>
                  <Col md={9}>
                    <div>
                      <Typography
                        type="quote-reduced"
                        color="#444"
                        tag="h4">
                        That would have been a nightmare to do manually. But with  Modana, it was easy.
                      </Typography>
                      <Typography
                        type="body"
                        css={theme => css`color: ${theme.color.primary};`}
                        tag="p">
                        Peter Spiderman
                      </Typography>
                    </div>
                  </Col>
                  <Col md={3}>
                    <div>
                      gambar
                    </div>
                  </Col>
                </Row>
              </Container>
            </TestimonyWrapper>
          </>
        )
      }}
    />
    
  )
}

export default Service