import React from 'react'
import styled from '@emotion/styled'
import { css } from '@emotion/core'
import { Typography } from 'reus'
import Img from 'gatsby-image'
import { Icon } from 'antd'

const LearnMoreButton = styled.button`
  height: 48px;
  border-radius: 26px;
  min-width: 150px;
  font-weight: bold;
  border: none;
  background-color: #fff0e8;
  text-align: center;
  text-transform: uppercase;
  color: ${({theme}) => theme.color.secondary};
`

const HeadWrapper = styled.div`
  margin-bottom: 80px;
  max-width: ${({theme}) => theme.breakPoints.md};
  margin-left: auto;
  margin-right: auto;
`

const Headline = ({title, desc}) => (
  <HeadWrapper>
    <Typography 
      tag="h2" 
      type="headline"
      css={theme =>  css`color: ${theme.color.secondary};`}
      align="center">
      {title}
    </Typography>
    {
      desc && 
        <Typography
          tag="p" 
          type="intro" 
          align="center">
          {desc}
        </Typography>
    }
  </HeadWrapper>
)

const ItemWrapper = styled.div`
  padding: 10px;
  text-align: center;
  margin-bottom: 20px;
  @media screen and (max-width: ${({theme}) => theme.breakPoints.sm}) {
    display: flex;
    align-items: center;
  }
`

const Item = ({image, title, desc, titleWeight}) => {
  return (
    <ItemWrapper>
      <div css={theme => css`
        @media screen and (max-width: ${theme.breakPoints.sm}) {
          & > div {
            margin-bottom: 0;
          }
        }
      `}>
        <Img css={css`margin-bottom: 21px;`} fixed={image.childImageSharp.fixed} />
      </div>
      <div css={theme => css`
        @media screen and (max-width: ${theme.breakPoints.sm}) {
          padding-left: 10px;
          p {
            text-align: left;
            margin-bottom: 0;
          }
        }
      `}>
        <Typography
          type="body"
          align="center"
          weight={titleWeight}
          tag="p">
          {title}
        </Typography>
      </div>
      {
        desc &&
        <Typography
          type="body"
          align="center"
          tag="p">
          {desc}
        </Typography>
      }
    </ItemWrapper>
  )
}

const TitleDesc = ({title, desc}) => (
  <>
    <Typography
      type="manifesto"
      css={theme => css`color: ${theme.color.primary};`}
      tag="h4">
      {title}
    </Typography>
    <Typography
      type="body"
      color="#444"
      tag="p">
      {desc}
    </Typography>
  </>
)

const ImgWrapper = styled.div`
  border-radius: 11px;
  box-shadow: 0px 8px 36px #d8d8d8;
  overflow: hidden;
`
const List = styled.ul`
  list-style: none;
  padding: 0;
`
const LinkArrow = ({text, className}) => (
  <a
    className={className}
    css={theme => css`
      color: ${theme.color.primary};
      text-transform: uppercase;
      font-weight: bold;
      display: block;
  `}>
    {text} 
    <Icon css={theme => css`
      color: ${theme.color.secondary};
      padding-left: 10px;
    `} color="" type="arrow-right" />
  </a>
)

export {
  LearnMoreButton,
  Item,
  Headline,
  TitleDesc,
  ImgWrapper,
  List,
  LinkArrow
}